<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
      <el-row>
                  <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="qichemingcheng">
          <el-input v-model="ruleForm.qichemingcheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.qichemingcheng" label="" prop="qichemingcheng">
              <el-input v-model="ruleForm.qichemingcheng" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="qicheleixing">
          <el-input v-model="ruleForm.qicheleixing" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.qicheleixing" label="" prop="qicheleixing">
              <el-input v-model="ruleForm.qicheleixing" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="yanse">
          <el-input v-model="ruleForm.yanse" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yanse" label="" prop="yanse">
              <el-input v-model="ruleForm.yanse" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="zuowei">
          <el-input v-model="ruleForm.zuowei" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.zuowei" label="" prop="zuowei">
              <el-input v-model="ruleForm.zuowei" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="pailiang">
          <el-input v-model="ruleForm.pailiang" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.pailiang" label="" prop="pailiang">
              <el-input v-model="ruleForm.pailiang" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                          <el-col :span="12">
        <el-form-item  v-if="type!='info'" label="" prop="riqi">
            <el-date-picker
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
                v-model="ruleForm.riqi" 
                type="date"
                placeholder="">
            </el-date-picker> 
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.riqi" label="" prop="riqi">
              <el-input v-model="ruleForm.riqi" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="yonghuming">
          <el-input v-model="ruleForm.yonghuming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yonghuming" label="" prop="yonghuming">
              <el-input v-model="ruleForm.yonghuming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.xingming" label="" prop="xingming">
              <el-input v-model="ruleForm.xingming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="shouji">
          <el-input v-model="ruleForm.shouji" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.shouji" label="" prop="shouji">
              <el-input v-model="ruleForm.shouji" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        </el-row>
                                                                                              <el-row>
            <el-col :span="24">
              <el-form-item v-if="type!='info'" label="" prop="beizhu">
                <el-input
                  style="min-width: 200px; max-width: 600px;"
                  type="textarea"
                  :rows="8"
                  placeholder=""
                  v-model="ruleForm.beizhu">
                </el-input>
              </el-form-item>
              <div v-else>
                <el-form-item v-if="ruleForm.beizhu" label="" prop="beizhu">
                    <span>{{ruleForm.beizhu}}</span>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
                                                                                                                                                                                                                              <el-form-item>
                <el-button v-if="type!='info'" type="primary" @click="onSubmit"></el-button>
        <el-button v-if="type!='info'" @click="back()"></el-button>
        <el-button v-if="type=='info'" @click="back()"></el-button>
      </el-form-item>
    </el-form>
    
    
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    let self = this
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',
            ruleForm: {
                        qichemingcheng: '',
                                qicheleixing: '',
                                yanse: '',
                                zuowei: '',
                                pailiang: '',
                                beizhu: '',
                                riqi: '',
                                yonghuming: '',
                                xingming: '',
                                shouji: '',
                      },
                                                                                                                                                        rules: {
                  qichemingcheng: [
                            { required: true, message: '', trigger: 'blur' },
                                                                                              ],
                  qicheleixing: [
                                                                                              ],
                  yanse: [
                                                                                              ],
                  zuowei: [
                                                                                              ],
                  pailiang: [
                                                                                              ],
                  beizhu: [
                                                                                              ],
                  riqi: [
                                                                                              ],
                  yonghuming: [
                                                                                              ],
                  xingming: [
                                                                                              ],
                  shouji: [
                                                                                              ],
              }
    };
  },
  props: ["parent"],
  computed: {
                                                                                                                              },
  methods: {
        // 
    download(file){
      window.open(`${file}`)
    },
    // 
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
                    if(o=='qichemingcheng'){
            this.ruleForm.qichemingcheng = obj[o];
            continue;
          }
                    if(o=='qicheleixing'){
            this.ruleForm.qicheleixing = obj[o];
            continue;
          }
                    if(o=='yanse'){
            this.ruleForm.yanse = obj[o];
            continue;
          }
                    if(o=='zuowei'){
            this.ruleForm.zuowei = obj[o];
            continue;
          }
                    if(o=='pailiang'){
            this.ruleForm.pailiang = obj[o];
            continue;
          }
                    if(o=='beizhu'){
            this.ruleForm.beizhu = obj[o];
            continue;
          }
                    if(o=='riqi'){
            this.ruleForm.riqi = obj[o];
            continue;
          }
                    if(o=='yonghuming'){
            this.ruleForm.yonghuming = obj[o];
            continue;
          }
                    if(o=='xingming'){
            this.ruleForm.xingming = obj[o];
            continue;
          }
                    if(o=='shouji'){
            this.ruleForm.shouji = obj[o];
            continue;
          }
                  }
                                                                                                                                                                              }
            // 
      this.$http({
        url: `${this.$storage.get('sessionTable')}/session`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          var json = data.data;
                                                                                                                                                                                              this.ruleForm.yonghuming = json.yonghuming
                                                this.ruleForm.xingming = json.xingming
                                                this.ruleForm.shouji = json.shouji
                              } else {
          this.$message.error(data.msg);
        }
      });
                                                                                                                                                                                                                  },
                                                                                        // 
                                                                                                                info(id) {
      this.$http({
        url: `sirendingzhi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
        // 
    onSubmit() {
                  // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                                                                                                                                                                                                                        this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `sirendingzhi/${!this.ruleForm.id ? "save" : "update"}`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = true;
                  this.parent.addOrUpdateFlag = false;
                  this.parent.sirendingzhiCrossAddOrUpdateFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.sirendingzhiCrossAddOrUpdateFlag = false;
    },
                                                                                                                              }
};
</script>
<style lang="scss">
.editor{
  height: 500px;
}
.amap-wrapper {
  width: 100%;
  height: 500px;
}
.search-box {
  position: absolute;
}
</style>
